/**
 * PrivateRoute.
 * 
 * Implement the Gatsby's WrapPageElement Browser API.
 * 
 * @see: https://www.gatsbyjs.org/docs/browser-apis/
 *
 * @param	Component:	  The Component (React component).
 * @param location:     The current url (relative path).
 * @param	props: 		    The Component properties.
 * 
 * @return Component:		Layout React Component (JSX).
 */

 /**
  * Dependencies.
  */
import React from 'react'
import PropTypes from 'prop-types'
import { navigate } from 'gatsby'
import { isAuthenticated } from '../services/auth'
import config from '../../gatsby-config'

/**
 * Conditional component.
 */
const PrivateRoute = ({ component: Component, location, ...rest }) => {

  // Skip if authentication is disabled from config.
  if (!config.siteMetadata.authentication.enable) {
    navigate('/')
    return null
  }

  // Redirect to login page if not authenticated.
  if( !isAuthenticated() && location.pathname !== '/account/login' ) {
    navigate('/account/login')
    return null
  }

  // Skip if authentication disabled from config.
  return <Component { ...rest } />

}

/**
 * Component properties.
 */
PrivateRoute.propTypes = {
  component: PropTypes.any.isRequired,
}

export default PrivateRoute