/**
 * ACCOUNT
 * 
 * @file        src/pages/account.jsx
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import { Link, navigate } from 'gatsby'
import { Router } from "@reach/router"
import PrivateRoute from '../components/privateRoute'
import Profile from '../components/account/profile'
import Login from '../components/account/login'
import { StateContext } from '../.gatsby/GlobalContextProvider'
import config from '../../gatsby-config'

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Account',
  description: 'Account stuff...',
}
 
const AccountLinks = () => {
  return (<><Link to="/account/profile">Profile</Link> / <Link to="/account/logout">Logout</Link></>)
}

/**
 * Page (React component - JSX).
 */
const Account = ({pageContext}) => {

    const context = useContext(StateContext)
    //const { matchPath } = usePageContext()
    //console.log('matchPath: ', matchPath)

    // Skip if authentication disabled.
    if (!config.siteMetadata.authentication.enable) {
      if( typeof window !== "undefined" ) navigate(-1)
      return null
    }
    
    // Account page.
    return (
      <>
      <div id="content" className="container col">
      <div className="content">
        <h1>{pageMeta.title}</h1>
        {context.authenticated ? (
          <p>Welcome to your account page {context.user.displayName}.</p>,
          <p><AccountLinks/></p>
        )
        :
          <p>By using our website, you agree with our <Link to="/privacy-policy/">Privacy Policy</Link> and <Link to="/terms-and-conditions/">Terms of service</Link>.</p>
        }
        <Router>
          <PrivateRoute exact path="/account/profile" component={Profile} />
          <Login path="/account/login" />
        </Router>
      </div>
    </div>
      </>
    )

}

Account.pageMeta = pageMeta

export default Account