/**
 * LOGIN.
 * 
 * @file        login.jsx
 * @location    components
 */

/**
 * Dependencies.
 */
import React from 'react'
import { Link as GatsbyLink, navigate } from 'gatsby'
import SocialLoginButton from './SocialLoginButton'
//import loadOneTapClient, { unloadOneTapClient } from '../google/OneTapClient'
import config from '../../../gatsby-config'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGoogle, faFacebook, faTwitter, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { Helmet } from 'react-helmet'
import '../../styles/login.css'

/**
 * FA ICON COMPONENTS.
 */
const FaGoogleIcon = () => <FontAwesomeIcon icon={faGoogle} />
const FaFacebookIcon = () => <FontAwesomeIcon icon={faFacebook} />
const FaTwitterIcon = () => <FontAwesomeIcon icon={faTwitter} />
const FaLinkedInIcon = () => <FontAwesomeIcon icon={faLinkedin} />

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Login',
  description: 'Login stuff...',
}

/**
 * LOGIN React Component (JSX).
 */
//class Login extends React.Component {
const Login = () => {

  if (!config.siteMetadata.authentication.enable) {
    navigate(-1)
    return null
  }

  // Skip page if already logged in.
  /*
  if( isLoggedIn() ) {
    navigate(`/account/profile`)
  }
  */

  // Show login page.
  return (
    <>
      <Helmet title={pageMeta.title} />
      <div className="box">
        <h2>{pageMeta.title}</h2>
        <p>Select your prefered social account to log in:</p>
        <SocialLoginButton id="google" title="Google" icon={<FaGoogleIcon />} />
        <SocialLoginButton id="facebook" title="Facebook" icon={<FaFacebookIcon />} />
        <SocialLoginButton id="twitter" title="Twitter" icon={<FaTwitterIcon />} />
        <SocialLoginButton id="linkedin" title="LinkedIn" icon={<FaLinkedInIcon />} />
        <p>
        <GatsbyLink id="privacy" to='/privacy-policy/'>
          Privacy Policy
        </GatsbyLink>
        </p>
      </div>
    </>
  )

}

Login.pageMeta = pageMeta

export default Login
