/**
 * PROFILE
 * 
 * @file        profile.jsx
 * @location    components
 */

/**
 * Dependencies.
 */
import React, {useContext} from 'react'
import { StateContext } from '../../.gatsby/GlobalContextProvider'
import { Helmet } from 'react-helmet'
import '../../styles/profile.css'

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Profile',
  description: 'Profile stuff...',
}

/**
 * React component (JSX).
 */
//class Profile extends React.Component {
const Profile = () => {

  /**
   * Authentication.
   */
  const context = useContext(StateContext)

  return (
    <>
      <Helmet title={pageMeta.title} />
      <div className="content">
        <h2>{pageMeta.title}</h2>
        <ul>
          <li>Name: {context.user.displayName}</li>
          <li>Email: {context.user.emails[0].value} ({context.user.emails[0].verified ? 'Verified' : 'Not verified'})</li>
          <li>Language: {context.user._json.locale}</li>
        {/*
        Object.keys(context.user).map((key, i) =>
          <li key={i}>{key}: {context.user[key]}</li>
        )
        */}
        </ul>
      </div>
    </>
  )

}

Profile.pageMeta = pageMeta

export default Profile